/**
 * Idempotency Key Management
 *
 * Prevents duplicate webhook processing when webhooks are retried by:
 * 1. Storing idempotency keys in Redis with TTL
 * 2. Detecting duplicate requests
 * 3. Returning cached responses for duplicates
 *
 * Uses Redis for fast distributed storage across serverless functions.
 */

import { redis } from '@/lib/ratelimit';
import { logWarn, logInfo } from '@/utils/logger';

/**
 * Idempotency key TTL (24 hours)
 * Webhooks are typically retried for 24 hours
 */
const IDEMPOTENCY_TTL = 24 * 60 * 60; // 24 hours in seconds

/**
 * Generate idempotency key from webhook data
 *
 * For Twilio: CallSid or MessageSid + CallStatus/MessageStatus
 * For Lob: Letter ID + event type
 */
export function generateIdempotencyKey(params: {
  source: 'twilio' | 'lob';
  resourceId: string; // CallSid, MessageSid, or Letter ID
  eventType: string;
}): string {
  return `idempotency:${params.source}:${params.resourceId}:${params.eventType}`;
}

/**
 * Check if request has been processed before
 *
 * Returns { processed: true, response } if duplicate
 * Returns { processed: false } if new request
 */
export async function checkIdempotency(
  key: string
): Promise<{
  processed: boolean;
  response?: any;
}> {
  try {
    // Check if key exists in Redis
    const cached = await redis.get(key);

    if (cached) {
      logWarn('Duplicate webhook detected - returning cached response', { key });
      return {
        processed: true,
        response: JSON.parse(cached as string),
      };
    }

    return { processed: false };
  } catch (error: any) {
    // If Redis is down, allow the request (safer than blocking)
    logWarn('Idempotency check failed - allowing request', error);
    return { processed: false };
  }
}

/**
 * Store processed webhook response
 *
 * Stores the response in Redis with TTL to prevent duplicate processing
 */
export async function storeIdempotentResponse(
  key: string,
  response: any
): Promise<void> {
  try {
    // Store response with 24-hour TTL
    await redis.setex(
      key,
      IDEMPOTENCY_TTL,
      JSON.stringify(response)
    );

    logInfo('Stored idempotent response', { key });
  } catch (error: any) {
    // Log but don't throw - storing idempotency is not critical
    logWarn('Failed to store idempotent response', error);
  }
}

/**
 * Delete idempotency key (for manual retry)
 *
 * Used by admin recovery system to allow manual retry of failed webhooks
 */
export async function deleteIdempotencyKey(key: string): Promise<void> {
  try {
    await redis.del(key);
    logInfo('Deleted idempotency key for manual retry', { key });
  } catch (error: any) {
    logWarn('Failed to delete idempotency key', error);
  }
}

/**
 * Get idempotency key info (for debugging)
 */
export async function getIdempotencyInfo(key: string): Promise<{
  exists: boolean;
  ttl?: number;
  response?: any;
}> {
  try {
    const cached = await redis.get(key);
    if (!cached) {
      return { exists: false };
    }

    const ttl = await redis.ttl(key);

    return {
      exists: true,
      ttl: ttl || undefined,
      response: JSON.parse(cached as string),
    };
  } catch (error: any) {
    logWarn('Failed to get idempotency info', error);
    return { exists: false };
  }
}

/**
 * Extract resource ID from webhook payload
 */
export function extractResourceId(params: {
  source: 'twilio' | 'lob';
  payload: any;
}): string | null {
  if (params.source === 'twilio') {
    // Try CallSid, MessageSid, SmsSid, RecordingSid
    return (
      params.payload.CallSid ||
      params.payload.MessageSid ||
      params.payload.SmsSid ||
      params.payload.RecordingSid ||
      null
    );
  }

  if (params.source === 'lob') {
    // Lob sends nested body.id
    return params.payload.body?.id || params.payload.id || null;
  }

  return null;
}

/**
 * Extract event type from webhook payload
 */
export function extractEventType(params: {
  source: 'twilio' | 'lob';
  payload: any;
}): string | null {
  if (params.source === 'twilio') {
    // Twilio uses different status fields
    return (
      params.payload.CallStatus ||
      params.payload.MessageStatus ||
      params.payload.SmsStatus ||
      params.payload.RecordingStatus ||
      null
    );
  }

  if (params.source === 'lob') {
    // Lob sends event_type
    return params.payload.event_type || null;
  }

  return null;
}
