/**
 * Twilio Call Status Webhook
 * POST /api/webhooks/twilio/call-status
 *
 * Receives call status updates from Twilio:
 * - initiated: Call has been created
 * - ringing: Phone is ringing
 * - answered: Call was answered
 * - completed: Call ended
 * - failed: Call failed
 * - busy: Recipient was busy
 * - no-answer: No answer
 *
 * Updates collection_attempts with call outcome
 */

import { NextRequest, NextResponse } from 'next/server';
import { db, Timestamp } from '@/lib/firebase';
import { verifyTwilioSignature } from '@/lib/twilio-verify';
import { logInfo, logError } from '@/utils/logger';
import { storeFailedWebhook, generateCorrelationId } from '@/lib/webhook-recovery';
import { validateWebhookOrigin, validateContentType } from '@/lib/csrf-protection';
import { checkWebhookRateLimit, getRateLimitHeaders } from '@/lib/webhook-ratelimit';
import { checkIdempotency, storeIdempotentResponse, generateIdempotencyKey } from '@/lib/idempotency';

export const dynamic = 'force-dynamic';

export async function POST(req: NextRequest): Promise<NextResponse> {
  const correlationId = generateCorrelationId();

  try {
    // 1. Rate limiting
    const rateLimit = await checkWebhookRateLimit({
      req,
      source: 'twilio',
      webhookType: 'call-status',
    });

    if (!rateLimit.allowed) {
      logError('Rate limit exceeded for Twilio call-status webhook', undefined, { correlationId });
      return NextResponse.json(
        { error: 'Too many requests' },
        {
          status: 429,
          headers: getRateLimitHeaders(rateLimit),
        }
      );
    }

    // 2. CSRF Protection: Validate origin (no Origin header = server-to-server)
    if (!validateWebhookOrigin(req)) {
      logError('CSRF: Invalid origin on webhook', undefined, { correlationId });
      return NextResponse.json({ error: 'Forbidden' }, { status: 403 });
    }

    // 3. Validate Content-Type (Twilio sends form data)
    if (!validateContentType(req, ['application/x-www-form-urlencoded'])) {
      logError('CSRF: Invalid Content-Type', undefined, { correlationId });
      return NextResponse.json({ error: 'Invalid Content-Type' }, { status: 400 });
    }

    // 4. Verify Twilio signature
    const isValid = await verifyTwilioSignature(req);
    if (!isValid) {
      logError('Invalid Twilio signature on call-status webhook', undefined, { correlationId });
      return NextResponse.json({ error: 'Invalid signature' }, { status: 401 });
    }

    // 5. Parse form data
    const formData = await req.formData();
    const callSid = formData.get('CallSid') as string;
    const callStatus = formData.get('CallStatus') as string;
    const callDuration = formData.get('CallDuration') as string;
    const from = formData.get('From') as string;
    const to = formData.get('To') as string;

    // 6. Check idempotency (prevent duplicate processing on retry)
    const idempotencyKey = generateIdempotencyKey({
      source: 'twilio',
      resourceId: callSid,
      eventType: callStatus,
    });

    const idempotency = await checkIdempotency(idempotencyKey);
    if (idempotency.processed) {
      logInfo('Duplicate Twilio webhook - returning cached response', {
        callSid,
        callStatus,
        correlationId,
      });
      return NextResponse.json(idempotency.response);
    }

    logInfo('Twilio call status update', {
      callSid,
      callStatus,
      callDuration,
      from,
      to,
      correlationId,
    });

    // 3. Find corresponding collection attempt
    const attemptsSnapshot = await db
      .collection('collection_attempts')
      .where('callSID', '==', callSid)
      .limit(1)
      .get();

    if (attemptsSnapshot.empty) {
      logError('Collection attempt not found for call SID', { callSid });
      // Still return 200 to prevent Twilio retries
      return NextResponse.json({ success: true });
    }

    const attemptDoc = attemptsSnapshot.docs[0];
    const updateData: any = {
      updatedAt: Timestamp.now(),
    };

    // 4. Update based on status
    switch (callStatus) {
      case 'initiated':
        updateData.callStartedAt = Timestamp.now();
        updateData.result = 'pending';
        break;

      case 'ringing':
        // No update needed, just log
        break;

      case 'in-progress':
        updateData.callStartedAt = Timestamp.now();
        updateData.result = 'pending';
        break;

      case 'completed':
        updateData.callEndedAt = Timestamp.now();
        updateData.callDuration = parseInt(callDuration || '0', 10);
        // Don't override result - it may be set by voice-ai webhook
        if (!attemptDoc.data().result || attemptDoc.data().result === 'pending') {
          updateData.result = 'success';
        }
        break;

      case 'failed':
      case 'busy':
      case 'no-answer':
        updateData.callEndedAt = Timestamp.now();
        updateData.result = 'failed';
        updateData.callOutcome = callStatus === 'busy' ? 'voicemail' : 'no_answer';
        updateData.callErrorMessage = `Call ${callStatus}`;
        break;

      case 'canceled':
        updateData.callEndedAt = Timestamp.now();
        updateData.result = 'failed';
        updateData.callErrorMessage = 'Call canceled';
        break;
    }

    // 5. Update attempt
    await attemptDoc.ref.update(updateData);

    logInfo('Collection attempt updated from call status', {
      attemptId: attemptDoc.id,
      callStatus,
    });

    // 7. Store idempotent response (prevent duplicate processing on retry)
    const response = { success: true };
    await storeIdempotentResponse(idempotencyKey, response);

    return NextResponse.json(response);
  } catch (error: any) {
    logError('Twilio call-status webhook error', error, { correlationId });

    // Store failed webhook for manual recovery
    try {
      const formData = await req.formData();
      const payload: Record<string, string> = {};
      formData.forEach((value, key) => {
        payload[key] = value.toString();
      });

      const headers: Record<string, string> = {};
      req.headers.forEach((value, key) => {
        headers[key] = value;
      });

      await storeFailedWebhook({
        source: 'twilio',
        eventType: 'call.status',
        payload,
        headers,
        url: req.url,
        error,
        correlationId,
      });
    } catch (recoveryError: any) {
      logError('Failed to store webhook for recovery', recoveryError, { correlationId });
    }

    // Return 200 to prevent Twilio from retrying
    return NextResponse.json({ success: true }, { status: 200 });
  }
}
